.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  background-color: white;
}
.container {
  margin-top: 0;
  margin-bottom: 1rem;
  background-color: #6ee7b7;
  height: 20%;
  font-size: larger;
  padding: 2.5rem;
  border-radius: 1.4rem;
}
.btn {
  margin: 0.5rem;
  border-bottom-style: groove;
  height: 2.5rem;
  width: 100px;
  border-radius: 1rem;
}
.navDiv {
  height: 15%;
  background-color: #d1fae5;
  padding: 1rem;
}
.list-nonbullet {
  list-style: none;
}
.listDiv {
  padding: 0.5rem;
  margin: 1rem;
  width: 80%;
  height: 50px;
  border-style: solid;
  border-color: #e5e7eb;
}
.list {
  text-align: left;
}
.list2 {
  font-size: smaller;
  text-align: left;
}
